/* ── Voting Page Shared Styles ─────────────────────────────── */

.roll-form {
  max-width: 400px;
  margin: 0 auto;
}

.roll-display {
  text-align: center;
  font-size: 1rem;
  margin-bottom: 12px;
  color: var(--gray-600);
  padding: 12px 20px;
  background: linear-gradient(
    135deg,
    rgba(99, 102, 241, 0.08),
    rgba(236, 72, 153, 0.06)
  );
  border-radius: var(--radius-sm);
  border: 1px solid rgba(99, 102, 241, 0.15);
}

.roll-display strong {
  color: var(--primary-dark);
  font-weight: 800;
}

/* ── Votes Counter ────────────────────────────────────────── */

.votes-remaining {
  text-align: center;
  font-size: 1.15rem;
  font-weight: 700;
  margin-bottom: 24px;
  color: var(--gray-700);
  padding: 14px 24px;
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.8),
    rgba(255, 255, 255, 0.5)
  );
  border-radius: var(--radius-sm);
  border: 2px solid var(--gray-200);
  backdrop-filter: blur(8px);
}

.votes-remaining span {
  display: inline-block;
  color: var(--primary);
  font-size: 1.4rem;
  font-weight: 900;
  min-width: 32px;
  transition: all 0.3s ease;
}

.votes-remaining .zero {
  color: var(--error);
  animation: pulse 1s ease infinite;
}

@keyframes pulse {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}

/* ── Batch Columns (responsive) ───────────────────────────── */

.batch-columns {
  display: flex;
  gap: 20px;
  justify-content: center;
  flex-wrap: nowrap;
}

.batch-column {
  flex: 1;
  min-width: 260px;
  max-width: 420px;
}

.batch-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14px 18px;
  margin-bottom: 10px;
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(8px);
  border: 1px solid var(--gray-200);
  border-radius: var(--radius-sm);
  transition: all 0.25s ease;
  position: relative;
  overflow: hidden;
}

/* Gradient left border on each individual row */
.batch-row::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 5px;
  background: linear-gradient(180deg, #3b82f6 0%, #6366f1 50%, #4f46e5 100%);
}

.batch-row:hover {
  background: rgba(255, 255, 255, 0.95);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.06);
  transform: translateX(4px);
}

.batch-info {
  display: flex;
  flex-direction: column;
  gap: 3px;
}

.batch-name {
  font-weight: 800;
  color: var(--gray-800);
  font-size: 0.95rem;
  letter-spacing: -0.2px;
}

.batch-leader {
  font-size: 0.78rem;
  color: var(--gray-500);
  font-weight: 500;
}

.vote-input {
  width: 68px;
  padding: 10px 10px;
  border: 2px solid var(--gray-200);
  border-radius: var(--radius-sm);
  text-align: center;
  font-size: 1.1rem;
  font-weight: 800;
  font-family: inherit;
  color: var(--primary-dark);
  background: rgba(255, 255, 255, 0.9);
  transition: all 0.25s ease;
}

.vote-input:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 4px var(--primary-glow);
  background: #fff;
}

.vote-input:hover {
  border-color: var(--primary-light);
}

/* Hide number input spinners/arrows */
.vote-input::-webkit-outer-spin-button,
.vote-input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.vote-input[type="number"] {
  -moz-appearance: textbox;
}

/* ── Vote Controls (Max button + input) ───────────────────── */

.vote-controls {
  display: flex;
  align-items: center;
  gap: 8px;
}

.btn-max {
  padding: 6px 12px;
  background: linear-gradient(135deg, var(--primary), var(--violet));
  color: #fff;
  border: none;
  border-radius: 6px;
  font-size: 0.72rem;
  font-weight: 700;
  font-family: inherit;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  transition: all 0.2s ease;
  box-shadow: 0 2px 6px var(--primary-glow);
  white-space: nowrap;
}

.btn-max:hover:not(:disabled) {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px var(--primary-glow);
}

.btn-max:disabled {
  opacity: 0.35;
  cursor: not-allowed;
  box-shadow: none;
}

/* ── Action Buttons ───────────────────────────────────────── */

.action-buttons {
  display: flex;
  justify-content: center;
  gap: 14px;
  margin-top: 28px;
}

.action-buttons .btn-primary {
  width: auto;
  padding: 13px 40px;
}

/* ── Success ──────────────────────────────────────────────── */

.success-section {
  text-align: center;
  padding: 44px 0;
  animation: bounceIn 0.4s ease;
}

.success-section .success-msg {
  font-size: 1.2rem;
  margin-bottom: 28px;
  font-weight: 700;
}

/* ── Subtitle ─────────────────────────────────────────────── */

.subtitle {
  text-align: center;
  color: var(--gray-500);
  margin-top: -12px;
  margin-bottom: 24px;
  font-size: 0.9rem;
  font-weight: 600;
}

/* ── Submitted Banner ─────────────────────────────────────── */

.submitted-banner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 16px;
  padding: 60px 24px;
  animation: fadeInUp 0.6s ease;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.submitted-banner .submitted-icon {
  font-size: 4rem;
  animation: popBounce 0.6s cubic-bezier(0.34, 1.56, 0.64, 1);
}

@keyframes popBounce {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  60% {
    transform: scale(1.3);
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.submitted-banner p {
  font-size: 1.35rem;
  font-weight: 800;
  color: #065f46;
  text-align: center;
}

.submitted-detail {
  font-size: 0.9rem !important;
  font-weight: 500 !important;
  color: var(--gray-500) !important;
}

.votes-status {
  text-align: center;
  color: var(--gray-600);
  line-height: 1.8;
  margin-bottom: 24px;
  font-size: 0.95rem;
}

/* ── Mobile: Single column ────────────────────────────────── */

@media (max-width: 1023px) {
  .batch-columns {
    flex-direction: column;
    align-items: stretch;
  }

  .batch-column {
    max-width: 100%;
    min-width: 0;
  }

  .action-buttons {
    flex-direction: column;
    align-items: stretch;
  }

  .action-buttons .btn-primary {
    width: 100%;
  }
}
