/* ═══════════════════════════════════════════════════════════
   Poster Votes — Cheerful & Vibrant Design System
   ═══════════════════════════════════════════════════════════ */

@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800;900&display=swap");

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --primary: #6366f1;
  --primary-light: #818cf8;
  --primary-dark: #4f46e5;
  --primary-glow: rgba(99, 102, 241, 0.35);

  --accent: #f59e0b;
  --accent-light: #fbbf24;

  --pink: #ec4899;
  --teal: #14b8a6;
  --violet: #8b5cf6;

  --gray-50: #f8fafc;
  --gray-100: #f1f5f9;
  --gray-200: #e2e8f0;
  --gray-300: #cbd5e1;
  --gray-400: #94a3b8;
  --gray-500: #64748b;
  --gray-600: #475569;
  --gray-700: #334155;
  --gray-800: #1e293b;
  --gray-900: #0f172a;

  --success: #10b981;
  --success-bg: #ecfdf5;
  --success-border: #a7f3d0;
  --error: #ef4444;
  --error-bg: #fef2f2;
  --error-border: #fecaca;

  --shadow-soft: 0 4px 24px rgba(0, 0, 0, 0.06);
  --shadow-glow: 0 8px 32px rgba(99, 102, 241, 0.12);
  --shadow-card:
    0 8px 40px rgba(0, 0, 0, 0.08), 0 0 0 1px rgba(255, 255, 255, 0.5) inset;

  --radius: 16px;
  --radius-sm: 10px;
  --radius-pill: 50px;
}

/* ── Animated Background ──────────────────────────────────── */

body {
  font-family:
    "Inter",
    -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    Roboto,
    sans-serif;
  min-height: 100vh;
  color: var(--gray-800);
  -webkit-font-smoothing: antialiased;
  background: #f0f2ff;
  background-image:
    radial-gradient(
      ellipse at 20% 0%,
      rgba(99, 102, 241, 0.12) 0%,
      transparent 50%
    ),
    radial-gradient(
      ellipse at 80% 100%,
      rgba(236, 72, 153, 0.1) 0%,
      transparent 50%
    ),
    radial-gradient(
      ellipse at 50% 50%,
      rgba(245, 158, 11, 0.06) 0%,
      transparent 60%
    );
  animation: bgShift 20s ease-in-out infinite alternate;
}

@keyframes bgShift {
  0% {
    background-color: #f0f2ff;
  }
  33% {
    background-color: #fdf2f8;
  }
  66% {
    background-color: #f0fdfa;
  }
  100% {
    background-color: #fefce8;
  }
}

/* Floating decorative orbs */
body::before,
body::after {
  content: "";
  position: fixed;
  border-radius: 50%;
  pointer-events: none;
  z-index: 0;
  filter: blur(80px);
  opacity: 0.5;
}

body::before {
  width: 500px;
  height: 500px;
  background: linear-gradient(
    135deg,
    rgba(99, 102, 241, 0.15),
    rgba(236, 72, 153, 0.1)
  );
  top: -150px;
  right: -100px;
  animation: float1 25s ease-in-out infinite;
}

body::after {
  width: 400px;
  height: 400px;
  background: linear-gradient(
    135deg,
    rgba(20, 184, 166, 0.12),
    rgba(245, 158, 11, 0.1)
  );
  bottom: -120px;
  left: -80px;
  animation: float2 30s ease-in-out infinite;
}

@keyframes float1 {
  0%,
  100% {
    transform: translate(0, 0) scale(1);
  }
  50% {
    transform: translate(-40px, 60px) scale(1.1);
  }
}

@keyframes float2 {
  0%,
  100% {
    transform: translate(0, 0) scale(1);
  }
  50% {
    transform: translate(50px, -40px) scale(1.15);
  }
}

/* ── Layout ───────────────────────────────────────────────── */

.page-center {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 68px);
  padding: 32px 24px;
  position: relative;
  z-index: 1;
}

.page-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 24px;
  position: relative;
  z-index: 1;
}

/* ── Cards ────────────────────────────────────────────────── */

.card {
  background: rgba(255, 255, 255, 0.75);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.8);
  border-radius: var(--radius);
  padding: 40px 36px;
  box-shadow: var(--shadow-card);
  width: 100%;
  max-width: 440px;
  animation: cardAppear 0.5s cubic-bezier(0.22, 1, 0.36, 1);
  position: relative;
  overflow: hidden;
}

/* Subtle gradient accent stripe at top */
.card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(
    90deg,
    var(--primary),
    var(--pink),
    var(--accent)
  );
  border-radius: var(--radius) var(--radius) 0 0;
}

@keyframes cardAppear {
  from {
    opacity: 0;
    transform: translateY(24px) scale(0.97);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.wide-card {
  max-width: 1000px;
  margin: 0 auto;
}

.card h2 {
  text-align: center;
  margin-bottom: 28px;
  font-size: 1.55rem;
  font-weight: 800;
  color: var(--gray-900);
  letter-spacing: -0.4px;
}

/* ── Form ─────────────────────────────────────────────────── */

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  font-size: 0.8rem;
  font-weight: 700;
  color: var(--gray-500);
  margin-bottom: 8px;
  text-transform: uppercase;
  letter-spacing: 0.8px;
}

.form-group input {
  width: 100%;
  padding: 13px 16px;
  border: 2px solid var(--gray-200);
  border-radius: var(--radius-sm);
  font-size: 0.95rem;
  font-family: inherit;
  color: var(--gray-800);
  background: rgba(255, 255, 255, 0.8);
  transition: all 0.25s ease;
}

.form-group input:hover {
  border-color: var(--primary-light);
  background: #fff;
}

.form-group input:focus {
  outline: none;
  border-color: var(--primary);
  background: #fff;
  box-shadow: 0 0 0 4px var(--primary-glow);
}

.form-group input::placeholder {
  color: var(--gray-400);
}

/* ── Buttons ──────────────────────────────────────────────── */

.btn-primary {
  display: block;
  width: 100%;
  padding: 13px 28px;
  background: linear-gradient(135deg, var(--primary), var(--violet));
  color: #fff;
  border: none;
  border-radius: var(--radius-sm);
  font-size: 0.95rem;
  font-weight: 700;
  cursor: pointer;
  font-family: inherit;
  letter-spacing: 0.3px;
  transition: all 0.25s ease;
  box-shadow: 0 4px 16px var(--primary-glow);
  position: relative;
  overflow: hidden;
}

.btn-primary::after {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(
    135deg,
    transparent 40%,
    rgba(255, 255, 255, 0.2) 50%,
    transparent 60%
  );
  transform: translateX(-100%);
  transition: transform 0.6s ease;
}

.btn-primary:hover:not(:disabled)::after {
  transform: translateX(100%);
}

.btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 28px var(--primary-glow);
}

.btn-primary:active:not(:disabled) {
  transform: translateY(0);
  box-shadow: 0 2px 8px var(--primary-glow);
}

.btn-primary:disabled {
  opacity: 0.45;
  cursor: not-allowed;
  box-shadow: none;
}

.btn-secondary {
  padding: 12px 28px;
  background: rgba(255, 255, 255, 0.7);
  color: var(--gray-700);
  border: 2px solid var(--gray-200);
  border-radius: var(--radius-sm);
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  font-family: inherit;
  transition: all 0.25s ease;
  backdrop-filter: blur(4px);
}

.btn-secondary:hover {
  background: rgba(255, 255, 255, 0.95);
  border-color: var(--primary-light);
  color: var(--primary-dark);
  transform: translateY(-1px);
  box-shadow: var(--shadow-soft);
}

.btn-danger {
  padding: 9px 20px;
  background: linear-gradient(135deg, #ef4444, #dc2626);
  color: #fff;
  border: none;
  border-radius: var(--radius-sm);
  font-size: 0.85rem;
  font-weight: 700;
  cursor: pointer;
  font-family: inherit;
  transition: all 0.2s ease;
  box-shadow: 0 3px 12px rgba(239, 68, 68, 0.25);
}

.btn-danger:hover {
  background: linear-gradient(135deg, #dc2626, #b91c1c);
  box-shadow: 0 6px 20px rgba(239, 68, 68, 0.35);
  transform: translateY(-1px);
}

.btn-sm {
  padding: 6px 14px;
  font-size: 0.8rem;
}

/* ── Messages ─────────────────────────────────────────────── */

.error-msg {
  background: var(--error-bg);
  color: #b91c1c;
  border: 1px solid var(--error-border);
  padding: 12px 18px;
  border-radius: var(--radius-sm);
  font-size: 0.88rem;
  font-weight: 600;
  margin-bottom: 16px;
  text-align: center;
  animation: shakeX 0.4s ease;
}

@keyframes shakeX {
  0%,
  100% {
    transform: translateX(0);
  }
  20% {
    transform: translateX(-8px);
  }
  40% {
    transform: translateX(8px);
  }
  60% {
    transform: translateX(-5px);
  }
  80% {
    transform: translateX(5px);
  }
}

.success-msg {
  background: var(--success-bg);
  color: #065f46;
  border: 1px solid var(--success-border);
  padding: 12px 18px;
  border-radius: var(--radius-sm);
  font-size: 0.88rem;
  font-weight: 600;
  margin-bottom: 16px;
  text-align: center;
  animation: bounceIn 0.4s ease;
}

@keyframes bounceIn {
  0% {
    opacity: 0;
    transform: scale(0.9);
  }
  60% {
    transform: scale(1.03);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

.loading-text,
.empty-text {
  text-align: center;
  color: var(--gray-400);
  padding: 36px 0;
  font-size: 0.95rem;
}

.text-muted {
  color: var(--gray-400);
}

/* ── Table ────────────────────────────────────────────────── */

.table-wrapper {
  overflow-x: auto;
  border-radius: var(--radius);
  border: 1px solid var(--gray-200);
  background: rgba(255, 255, 255, 0.6);
}

table {
  width: 100%;
  border-collapse: collapse;
}

thead th {
  background: linear-gradient(135deg, var(--gray-900), var(--gray-800));
  color: #e2e8f0;
  font-size: 0.73rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 14px 16px;
  text-align: left;
}

thead th:first-child {
  border-radius: var(--radius) 0 0 0;
}

thead th:last-child {
  border-radius: 0 var(--radius) 0 0;
}

tbody td {
  padding: 13px 16px;
  font-size: 0.9rem;
  border-bottom: 1px solid var(--gray-100);
  transition: background 0.2s;
}

tbody tr {
  transition: all 0.2s ease;
}

tbody tr:hover {
  background: linear-gradient(
    90deg,
    rgba(99, 102, 241, 0.05),
    rgba(236, 72, 153, 0.03)
  );
  transform: scale(1.005);
}

tbody tr:last-child td {
  border-bottom: none;
}

tbody tr:last-child td:first-child {
  border-radius: 0 0 0 var(--radius);
}

tbody tr:last-child td:last-child {
  border-radius: 0 0 var(--radius) 0;
}

/* ── Role badges ──────────────────────────────────────────── */

.role-badge {
  display: inline-block;
  padding: 5px 14px;
  border-radius: var(--radius-pill);
  font-size: 0.7rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.6px;
}

.role-admin {
  background: linear-gradient(135deg, #dbeafe, #e0e7ff);
  color: #3730a3;
}

.role-user {
  background: linear-gradient(135deg, #ecfdf5, #d1fae5);
  color: #065f46;
}

/* ── Sort Controls ────────────────────────────────────────── */

.sort-controls {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-bottom: 24px;
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--gray-500);
  flex-wrap: wrap;
}

.sort-controls .btn-primary,
.sort-controls .btn-secondary {
  width: auto;
  padding: 10px 22px;
  font-size: 0.85rem;
}

.sort-controls .btn-primary {
  box-shadow: 0 3px 12px var(--primary-glow);
}

/* ── Responsive ───────────────────────────────────────────── */

@media (max-width: 768px) {
  body::before,
  body::after {
    display: none;
  }

  .page-center {
    padding: 20px 14px;
  }

  .page-container {
    padding: 20px 14px;
  }

  .card {
    padding: 28px 22px;
    border-radius: 14px;
  }

  .wide-card {
    max-width: 100%;
  }

  .sort-controls {
    gap: 8px;
  }
}
